.app_header {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 15px;
  box-shadow: 0 5px 5px rgba(0, 21, 41, 0.08);
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.app_header .menu_float {
  display: flex;
  justify-content: center;
  align-items: center;
}
.app_header .menu_float .ant-breadcrumb {
  margin-left: 10px;
}
.app_header .menu_float .trigger {
  font-size: 15px;
}
.app_header .menu_float .trigger:hover {
  color: #1890ff;
}
.app_header .menu_float .menu_icon {
  padding: 0 12px;
  cursor: pointer;
}
.app_header .menu_float .menu_icon:hover {
  background-color: #efefef;
}
